
const styles = {
    container : {
        backgroundColor: '#cfd8dc',
        display: 'flex',
        height: '90vh',        
        padding: '0 5%',
        flexDirection: 'row'
    },

    input : {
        backgroundColor: 'rgba(0,0,0,0.2)',
        height: 30,
        border: 'none',
        margin: '10px 0',
        width: 300,
        borderRadius: '10px',
        paddingLeft: '10px'
    },

    btn: {
        height: 40,
        width: 310,
        backgroundColor : '#607d8b',
        color: '#fff',
        fontWeight: '600',
        fontSize: '1.2rem',
        border: 'none',
        borderRadius: '10px',
        marginTop: '20px',
        webkitBoxShadow: '0px 10px 11px -8px rgba(0,0,0,0.75)',
        mozBoxShadow: '0px 10px 11px -8px rgba(0,0,0,0.75)',
        boxShadow: '0px 10px 11px -8px rgba(0,0,0,0.75)'
    }
}


export default styles


